//Конвертируется в .css в папку /css/

@import '_fonts';
@import '_media';

@keyframes slideShow{
    0%{
        width: 0px;
    }
    100%{
        width: 230px;
    }
}

body{
    background-color: #311432;
    height: 100%;
    color: white;
}
a{
    text-decoration: none;
}
.logo{
    @include BoldUpFonts;
}
.header{
    margin-top: 20px;
}
.menu{
    margin-top: 10px;
    display: flex;
    float: right;
    list-style-type: none;
}
.menu_nav{
    margin-right: 30px;
    @include BoldUpFonts;
    font-size: 20px;
    transition: .2s ease;
    cursor: pointer;
}
.menu_nav:hover{
    color: #7331FF;
}
.block_hello{
    margin-top: 60px;
    width: 100%;
    height: 300px;
    background-image: url(../assets/create-img.png);
    background-position: center;
}
.block_information{
    text-align: center;
    padding-top: 110px;
    font-weight: 700;
    @include BoldDownFonts;
}
.registration{
    font-weight: 700;
    @include BoldDownFonts;
    font-size: 40px; 
}
.block_message{
    margin-top: 50px;
    text-align: center;
}
.inf{
    @include RegularDownFonts;
    font-size: 17px;
    font-weight: 700;
}
.btn-test{
    @include BoldUpFonts;
    font-size: 15px;
    border: none;
    background-color: #259F38;
    padding: 15px 60px;
    color: white;
    transition: .2s ease;
    letter-spacing: 2px;
}
.btn-test:hover{
    box-shadow: 0px 0px 20px 0px rgb(83 78 78);
}
.block_btn{
    margin-top: 10px;
    text-align: center;
}

//footer
.footer{
    position: absolute;
    bottom: 0;
    margin-top: 100px;
	width: 100%;
	height: 80px;
}
.block_social{
    text-align: center;
    margin-bottom: 10px;
}
.vk-hover{
    font-size: 35px;
    color: white;
    margin-right: 30px;
    transition: .2s ease;
}
.vk-hover:hover{
    color: rgb(0, 119, 255);
}
.discord-hover{
    font-size: 35px;
    color: white;
    transition: .2s ease;
}
.discord-hover:hover{
    color: #5865f2
}
.name_project{
    @include BoldUpFonts;
    font-size: 20px;
    text-align: center;
}
//burger-menu
.burger-line-one{
    width: 25px;
    height: 2px;
    margin-bottom: 5px;
    background-color: white;
}
.burger-line-two{
    width: 25px;
    height: 2px;
    margin-bottom: 5px;
    background-color: white;
}
.burger-line-three{
    width: 25px;
    height: 2px;
    background-color: white;
}
.burger-menu{
    display: none;
    width: 230px;
    height: 100%;
    position: fixed;
    background-color: #7331ff;
    top: 0;
    right: 0;
    text-align: center;
    animation-name: slideShow;
    animation-duration: .5s;
}
.burger{
    display: none;
}
.menu_nav_mobile{
    list-style-type: none;
    @include BoldUpFonts;
    font-size: 20px;
    transition: .2s ease;
    cursor: pointer;
}
.menu_nav_mobile:hover{
    color: #7331FF;
}
.block_mobile_nav{
    margin-top: 300px;
}
//media
@media screen and (max-width: 768px){
    .menu_nav{
        display: none;
    }
    .burger{
        display: block;
    }
}
@media screen and (max-width: 425px){
    .burger{
        margin-top: -45px;
    }
}

